.mainContainer {
  display: flex;
  justify-content: center;
  gap: 50px;
  padding-top: 40px;
  
}

.imageContainer {
  flex-basis: 22%;
  max-width: 22%;
  
}
.descriptionContainer{
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.descriptionMini{
    font-size: 18px;
}

@media screen and (max-width: 768px) {
  .mainContainer {
    display: flex;
    flex-direction: column;
  }
  .imageContainer {
    flex-basis: 100%;
    max-width: 100%;
  }
  .mainContainer{
    padding-bottom: 40px;
    padding-left: 10px;
    padding-right: 10px;
  }
}

/* CSS */
.button {
  appearance: none;
  background-color: transparent;
  border: 2px solid #1a1a1a;
  border-radius: 8px;
  box-sizing: border-box;
  color: #3b3b3b;
  cursor: pointer;
  display: inline-block;
  font-family: Roobert, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
  margin: 0;
  min-height: 60px;
  min-width: 0;
  outline: none;
  padding: 16px 24px;
  text-align: center;
  text-decoration: none;
  transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 100%;
  will-change: transform;
}

.button:disabled {
  pointer-events: none;
}

.button:hover {
  color: #fff;
  background-color: #1a1a1a;
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  transform: translateY(-2px);
}

.button:active {
  box-shadow: none;
  transform: translateY(0);
}
.descriptionMini{
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding-bottom: 20px;
  color: rgb(51, 51, 51);
}
.openedImg {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.openedImg img {
  max-width: 80%;
  max-height: 80%;
  object-fit: contain;
}