.card-container {
  display: flex;
  flex-wrap: wrap;
  z-index: -2;
  justify-content: center;
}

.card {
  margin: 20px;
  
  padding: 20px;
  cursor: pointer;
}
.card:hover{
  box-shadow: 0 4px 24px hsla(230, 75%, 40%, 0.4);

}
.card-img {
  width: 13rem;
  margin-bottom: 1rem;
}

.card-title {
  margin-bottom: 1rem;
}

.card-reviews {
  margin-bottom: 1rem;
  display: flex;
}

.rating-star {
  color: #d5ab55;
}

.total-reviews {
  font-size: 0.9rem;
  margin-left: 10px;
}

.card-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bag-icon {
  color: #535353;
}
