*{
    font-style: normal
}

.headerContainer{
    display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  align-content: normal;
  height: 72px;
  width: 100%;
  box-sizing: border-box;
    /* box-shadow: 0 0 10px rgba(0,0,0,0.5);  */
    border-bottom: 2px solid #f3f3f3;

    padding-left: 10px;
    padding-right: 10px;
    
    
}

.storeContainer{
    
display: flex;
gap:15px;
padding-left: 10px;

font-family: William Text Regular;
    font-variant-numeric: lining-nums;
    letter-spacing: .1em;
}
.storeContainer1{
    z-index: 1001;
}


.userContainer{
    display: flex;
    gap:10px;
    padding-right: 10px;

    font-family: William Text Regular;
    font-variant-numeric: lining-nums;
    letter-spacing: .1em;
    
}

.logo{
    font-family:Comic Sans MS, Comic Sans, cursive;
    font-size: 20px;
    color: black;
    text-decoration: none;
    letter-spacing: .1em;

    position: absolute;
    top: 21px;
    left: 50%;
    transform: translate(-50%);
}
.profile a{
  color:var(--text-color)
   
    
}


.search { 
    display: flex; 
    align-items: center; 
    justify-content: center; 
}
.busket a{
  color:var(--text-color)
    
}
.sidebar {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: white;
    z-index: 1000;
    padding: 20px;
    box-sizing: border-box;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;
  }
  
  .sidebar.open {
    transform: translateX(0);
  }

  



  @media only screen and (max-width: 768px) {
    .headerContainer{
        display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between;
      align-items: center;
      align-content: normal;
      height: 72px;
      width: 100%;
      box-sizing: border-box;
        /* box-shadow: 0 0 10px rgba(0,0,0,0.5);  */
        border-bottom: 2px solid #f3f3f3;
    
        padding-left: 10px;
        padding-right: 10px;
        
        
    }
  }

  
  
  .result{
    position: absolute;
    top: 70px;
    left: 0;
    z-index: 50;
    width: 100%;
    background-color: white;
    
  }
  
